import Scorer from './Scorer';

const TopScorers = props => {
  const topScorers = props.topScorers.map(scorer => {
    <Scorer scorer={scorer} />;
  });

  const test = 'testy!';

  return (
    <>
      <h2>Top Scorers</h2>
      {test}
    </>
  );
};

export default TopScorers;
